.products-wrap {
    padding: 50px 0;
}

.content-section {
    margin: 60px 0;
    border-bottom: 1px solid #dddddd;
    padding-bottom: 60px;
  }
  
  .section-title h2 {
    margin-top: 0;
    margin-bottom: 50px;
  }
  
  .product-content {
    background-color: white;
    border: 1px solid #dddddd;
  }
  .product-content h5 {
    font-size: 16px;
    text-transform: uppercase;
    margin: 0;
  }
  .product-content h5 a {
    color: #2a3f52;
  }
  .product-content h5 a:hover {
    color: #e84c3d;
  }
  .product-content span {
    color: #2a80b9;
    display: block;
  }
  .product-content span.price {
    margin-top: 10px;
    font-size: 16px;
    font-weight: 600;
  }
  
  .product-thumb img {
    width: 100%;
    height: 100%;
  }
  
  .product-item-1 {
    margin-bottom: 30px;
  }
  .product-item-1 .product-content {
    border-top: 0;
    padding: 20px 20px 15px 20px;
  }
  .product-item-1 .product-thumb {
    overflow: hidden;
  }
  
  .product-item-2 {
    margin-bottom: 30px;
    overflow: hidden;
    position: relative;
  }
  .product-item-2 .product-thumb {
    float: left;
    width: 100%;
  }
  .product-item-2 .product-thumb img {
    width: 100%;
    height: 100%;
  }
  .product-item-2 .product-content {
    float: right;
    border-left: 0;
    padding: 20px;
    overflow: hidden;
  }
  .product-item-2 .product-content.overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    color: white;
    opacity: 0;
    filter: alpha(opacity=0);
    transform: scale(0);
    -moz-transform: scale(0);
    -o-transform: scale(0);
    -webkit-transform: scale(0);
    -ms-transform: scale(0);
    transition: all 250ms ease-in-out;
  }
  .product-item-2 .product-content.overlay a {
    color: #e84c3d;
  }
  .product-item-2:hover .product-content.overlay {
    opacity: 1;
    filter: alpha(opacity=100);
    transform: scale(1);
    -moz-transform: scale(1);
    -o-transform: scale(1);
    -webkit-transform: scale(1);
    -ms-transform: scale(1);
  }
  
  .product-item-3 {
    margin-bottom: 30px;
  }
  .product-item-3 .product-thumb {
    overflow: hidden;
  }
  .product-item-3 .product-thumb img {
    width: 100%;
  }
  .product-item-3 .product-content {
    border-top: 0;
    padding: 20px 20px 0 20px;
  }
  .product-item-3 .button-holder {
    width: 70%;
    height: 26px;
    text-align: center;
    background-color: #e84c3d;
    margin: 0 auto;
    display: block;
    margin-top: 30px;
    -webkit-backface-visibility: hidden;
    /* Chrome, Safari, Opera */
    backface-visibility: hidden;
  }
  .product-item-3 .button-holder a {
    color: white;
    display: block;
    width: 100%;
    height: 100%;
    line-height: 26px;
    font-size: 24px;
  }
  .product-item-3 label {
    width: 60px;
    font-weight: normal;
  }
  .product-item-3 select, .product-item-3 option {
    color: #a3a7aa;
    border: 1px solid #c4c4c4;
  }
  .product-item-3 select:focus, .product-item-3 option:focus {
    outline: 0;
  }
  
  .product-item-4 {
    overflow: hidden;
    position: relative;
  }
  .product-item-4 .product-thumb {
    float: left;
    width: 100%;
  }
  .product-item-4 .product-content {
    float: right;
    border-left: 0;
    padding: 20px;
    overflow: hidden;
  }
  .product-item-4 .product-content.overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    color: white;
    opacity: 0;
    filter: alpha(opacity=0);
    transform: scale(0);
    -moz-transform: scale(0);
    -o-transform: scale(0);
    -webkit-transform: scale(0);
    -ms-transform: scale(0);
    transition: all 250ms ease-in-out;
  }
  .product-item-4 .product-content.overlay a {
    color: #e84c3d;
  }
  .product-item-4:hover .product-content.overlay {
    opacity: 1;
    filter: alpha(opacity=100);
    transform: scale(1);
    -moz-transform: scale(1);
    -o-transform: scale(1);
    -webkit-transform: scale(1);
    -ms-transform: scale(1);
  }
  
  .product-item {
    -webkit-backface-visibility: hidden;
    /* Chrome, Safari, Opera */
    backface-visibility: hidden;
    margin-bottom: 30px;
  }
  .product-item .product-content {
    border: 0;
    margin-top: 20px;
  }
  
  .product-item-vote {
    -webkit-backface-visibility: hidden;
    /* Chrome, Safari, Opera */
    backface-visibility: hidden;
    margin-bottom: 40px;
  }
  .product-item-vote .product-content {
    border: 0;
    margin-top: 20px;
  }
  .product-item-vote ul.progess-bars {
    padding: 0 !important;
    margin: 20px 0 0 0;
  }
  .product-item-vote ul.progess-bars li {
    list-style: none !important;
  }
  .product-item-vote ul.progess-bars .progress {
    margin-bottom: 10px;
    background: transparent;
    box-shadow: none;
    border-radius: 0;
    background-clip: padding-box;
    /* stops bg color from leaking outside the border: */
  }
  .product-item-vote ul.progess-bars .progress span {
    margin-left: 7px;
    display: inline-block;
  }
  .product-item-vote ul.progess-bars .progress span i {
    margin-left: 7px;
    display: inline-block;
  }
  .product-item-vote ul.progess-bars .progress span.comments {
    color: #e84c3d;
  }
  .product-item-vote ul.progess-bars .progress-bar {
    box-shadow: none;
  }
  .product-item-vote ul.progess-bars .progress-bar.comments {
    background-color: #e84c3d;
  }
  
  .ads-placemenet {
    overflow: hidden;
  }
  .ads-placemenet img {
    width: 100%;
  }
  
  .product-image {
    overflow: hidden;
  }
  .product-image img {
    width: 100%;
  }
  
  .product-information h2 {
    margin: 20px 0;
  }
  .product-information .product-infos {
    color: #2a80b9;
    font-size: 18px;
    font-weight: 600;
    margin: 30px 0;
  }
  .product-information .product-infos span {
    margin-right: 30px;
  }
  
  ul.product-buttons li {
    margin-bottom: 15px;
  }
  
  .main-btn {
    display: inline-block;
    background-color: #2a80b9;
    color: white;
    font-size: 16px;
    font-weight: 600;
    width: 160px;
    height: 46px;
    line-height: 46px;
    text-align: center;
    border-radius: 4px;
    background-clip: padding-box;
    /* stops bg color from leaking outside the border: */
  }
  .main-btn:hover {
    background-color: #e84c3d;
    color: white;
  }
  
  form input[type="text"], 
  form input[type="email"], 
  form input[type="number"], 
  form input[type="password"],
  textarea {
    background: #fff;
    border: 1px solid #ddd;
    padding: 12px 15px;
  }
  
  #contactform textarea,
  #contactform input {
    width: 100%;
  }
  
  #contactform textarea {
    min-height: 140px;
  }
  
  #contactform input.mainBtn {
    border: 0;
    outline: 0;
    padding: 12px;
    color: #fff;
    background: #2a80b9;
  }
  
  
  
  /* ==========================================================================
      Footer Site Styling
     ========================================================================== */
  .site-footer {
    color: white;
    font-size: 13px;
  }
  .site-footer a {
    color: white;
  }
  .site-footer a:hover {
    color: #bfddf0;
  }
  .site-footer h3.widget-title {
    font-weight: 600;
    color: white;
    margin: 0 0 30px 0;
    font-size: 1.4em;
  }
  
  .footer-widget {
    margin-bottom: 40px;
  }
  
  .our-partner {
    background-color: #2a80b9;
    padding: 15px;
    position: relative;
  }
  .our-partner .item {
    margin: 0 15px;
    overflow: hidden;
  }
  .our-partner .item img {
    width: 100%;
  }
  .our-partner .customNavigation .prev, .our-partner .customNavigation .next {
    position: absolute;
    top: 20%;
    font-size: 24px;
  }
  .our-partner .customNavigation .prev {
    left: -10px;
  }
  .our-partner .customNavigation .next {
    right: -10px;
  }
  
  .main-footer {
    background-color: #2d3e50;
    padding: 50px 0 0 0;
  }
  .main-footer ul {
    padding: 0;
    margin: 0;
    list-style: square;
    list-style-position: inside;
  }
  .main-footer ul li a {
    display: inline-block;
    line-height: 20px;
    margin-bottom: 10px;
  }
  
  ul.follow-us {
    margin-top: 30px;
  }
  ul.follow-us li {
    display: block;
    margin-bottom: 10px;
  }
  ul.follow-us li a {
    background-color: #2a80b9;
    color: white;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 16px;
    padding: 10px 25px;
    display: inline-block;
    border: 1px solid white;
  }
  ul.follow-us li a i {
    margin-right: 25px;
  }
  
  .newsletter {
    padding: 20px;
    background-color: #2a80b9;
  }
  .newsletter p {
    margin: 0 0 20px 0;
    font-size: 14px;
    text-align: left;
  }
  .newsletter input {
    -webkit-backface-visibility: hidden;
    /* Chrome, Safari, Opera */
    backface-visibility: hidden;
    outline: 0;
    border: 0;
    height: 38px;
    padding: 0 10px;
    width: 64%;
  }
  .newsletter input[type=submit] {
    width: auto;
    background-color: #2d3e50;
    color: white;
    padding: 0 25px;
    font-size: 14px;
    text-transform: uppercase;
    font-weight: 700;
    margin: 0;
  }
  
  .bottom-footer {
    background-color: #2d3e50;
    border-top: 1px solid #3598db;
    padding: 20px 0;
    font-size: 12px;
  }
  .bottom-footer span {
    display: block;
    margin-bottom: 10px;
  }
  .bottom-footer p {
    width: 70%;
    margin: 0 auto;
  }

